import Layout from '../components/layout';

const Projects = () => (

    <div>
        <Layout>
            <section className="content">
                <div className="row">
                    <div className="offset-lg-3 col-lg-6">
                        <div className="paper">
                            <h1 className="written text-center">my work.</h1>
                            <p>
                                Below are a series of projects that I've either fully developed or contributed to the development of.
                            </p>
                        </div>
                    </div>
                </div>
                <hr></hr>
                {/* Enroute */}
                <div className="row">
                    <div className="col-12">
                        <h2 className="text-center">Air Canada Enroute</h2>
                    </div>
                </div>
                <div className="row enroute">
                    <div className="col-lg-6">
                        <img className="img-fluid" src="assets/images/enroute.png"></img>
                    </div>
                    <div className="col-lg-6">

                    </div>
                </div>
                {/* Sierra Management Group */}
                <div className="row">
                    <div className="col-12">
                        <h2 className="text-center">Sierra Management Group</h2>
                    </div>
                </div>
                <div className="row smg">
                    <div className="col-lg-6">
                        <img className="img-fluid" src="assets/images/smg.png"></img>
                    </div>
                    <div className="col-lg-6">
                        <div className="row">
                            <div className="col-12">
                                <h3><a href="https://sierramanagementgroup.com/" target="_blank">www.sierramanagementgroup.com</a></h3>
                                <h4>LBC IT Solutions Inc.</h4>
                                <p>
                                    I fully developed this website using WordPress and a custom-tailored theme which used isotope.js to layout elements based 
                                    on design concepts presented by the client. I customized the existing theme using the industry standard child-theme method 
                                    in order to satisfy client requirements. <br></br>
                                    Through this customization I:
                                    <ul>
                                        <li>altered the layout options from 3 to 4</li>
                                        <li>modified the dynamic HTML of existing components</li>
                                        <li>created components for new use cases</li>
                                        <li>changed the JQuery animations</li>
                                    </ul>
                                    I worked with the client through Trello in order to manage tasks, recieve feedback and discuss design options. When the site
                                    was completed I created documentation which demonstrated the steps to add or alter the content and modify the layout of the website.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </Layout>
        <style jsx>{`
            .enroute {
                background-color: #8dacbf'
            }
            .smg {
                background-color: #242423;
                color: #fff;
            }

            a {
                text-decoration: none;
                color: inherit;
            }

            a:hover {
                border-bottom: 1.5px solid;
            }
        `}</style>
    </div>
);

export default Projects;